@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins',sans-serif;
}

.container-reg{
  max-width: 700px;
  width: 100%;
  height: 95%;
  background-color: #fff;
  padding: 25px 30px;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.15);
  margin-left: 60px;
  overflow-y: scroll;
}

::-webkit-scrollbar {
  width: 10px;
}
.container-reg .title{
  font-size: 25px;
  font-weight: 500;
  position: relative;
}
.container-reg .title::before{
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 3px;
  width: 30px;
  border-radius: 5px;
  background: linear-gradient(135deg, #2f5e3d, #5078ad);
}
.content form .user-details{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0 12px 0;
}

form .user-details .input-box{
  margin-bottom: 15px;
  width: calc(100% / 2 - 20px);
}
form .input-box span.details{
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.user-details .input-box input{
  height: 45px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border-radius: 15px;
  padding-left: 15px;
  border: 1px solid #ccc;
  border-bottom-width: 2px;
  transition: all 0.3s ease;
}
.user-details .input-box input:focus,
.user-details .input-box input:valid{
  border-color: #5078ad;
}


form .user-details .input-box-reasons{
  margin-bottom: 15px;
  width: calc(100%);
}
form .input-box-reasons span.details{
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.user-details .input-box-reasons textarea{
  height: 100px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border-radius: 5px;
  padding-left: 15px;
  border: 1px solid #ccc;
  border-bottom-width: 2px;
  transition: all 0.3s ease;
}
.user-details .input-box-reasons textarea:focus,
.user-details .input-box-reasons textarea:valid{
  border-color: #5078ad;
}
 form .gender-details .gender-title{
  font-size: 20px;
  font-weight: 500;
 }
 form .category{
   display: flex;
   width: 80%;
   margin: 14px 0 ;
   justify-content: space-between;
 }
 form .category label{
   display: flex;
   align-items: center;
   cursor: pointer;
 }
 form .category label .dot{
  height: 18px;
  width: 18px;
  border-radius: 50%;
  margin-right: 10px;
  background: #d9d9d9;
  border: 5px solid transparent;
  transition: all 0.3s ease;
}
 #dot-1:checked ~ .category label .one,
 #dot-2:checked ~ .category label .two,
 #dot-3:checked ~ .category label .three{
   background: #9b59b6;
   border-color: #d9d9d9;
 }
 form input[type="radio"]{
   display: none;
 }
 .button{
   height: 45px;
   margin: 35px 0
 }
 .button input{
   height: 100%;
   width: 100%;
   border-radius: 20px;
   border: none;
   color: #fff;
   font-size: 18px;
   font-weight: 500;
   letter-spacing: 1px;
   cursor: pointer;
   transition: all 0.3s ease;
   background: linear-gradient(135deg, #2f5e3d, #5078ad);
 }
  .button input:hover{
  /* transform: scale(0.99);  */
  background: linear-gradient(-135deg, #2f5e3d, #5078ad);
  }
 @media(max-width: 584px){
 .container{
  max-width: 100%;
}
form .user-details .input-box{
    margin-bottom: 15px;
    width: 100%;
  }
  form .category{
    width: 100%;
  }
  .content form .user-details{
    max-height: 300px;
    overflow-y: scroll;
  }
  .user-details::-webkit-scrollbar{
    width: 5px;
  }
  }
  @media(max-width: 459px){
  .container .content .category{
    flex-direction: column;
  }
} 

.reg-message {
  color: black;
  font-size: 16px;
  margin-left: 170px;
}

.link {
  text-decoration: none;
  color: #2f5e3d;
  font-weight: 500;
}

.option{
  margin-top: 10px;
  font-size: larger;
  font-weight: 500;
  padding: 5px;
  border-style:solid;
  border: 1;
  border-radius: 15px;
  border-color: white;
}

.option:hover{
  border-style:solid;
  border: 1;
  border-radius: 15px;
  border-color: #2f5e3d;
}

.typeName{
  margin-left: 5px;
}

.description{
  font-weight:normal;
  font-size: small;
  font-style: italic;
}

